import Vue from 'vue'
import Vuex from 'vuex'

import toyService from '../service/toys.service.js'

Vue.use(Vuex)

const toyStore = {


    state: {
        toys: null,
        toy: null
        // filterdToys
    },
    getters: {
        toy(state) {
            return state.toy
        },
        toys(state) {
            return state.toys
        }
    },
    mutations: {
        setToys(state, { toys }) {
            state.toys = toys
            console.log(toys);

        }, setToy(state, { toy }) {
            console.log('added', toy);
            state.toy = toy
        }, deleteToy(state, { toyId }) {
            const idx = state.toys.findIndex((toy) => toy._id === toyId)
            state.toys.splice(idx, 1)
        },
        // filterdToys(state, { filterdToys }) {
        //   state.filterdToys = filterdToys
        // }
    },
    actions: {
        getToys({ commit }) {
            toyService.query().then((toys) => commit({ type: 'setToys', toys }))
        },
        getToy({ commit }, { toyId }) {
            return toyService.getToyById(toyId).then((toy) => {
                commit({ type: 'setToy', toy })
            })
        },
        deleteToy({ commit }, { toyId }) {
            toyService.deleteToy(toyId).then(() => commit({ type: 'deleteToy', toyId }))
        },
        updateToy({ commit }, { toy }) {

            toyService.updateToy(toy)
        },
        addToy({ commit }, { toy }) {
            toyService.addToy(toy)
        },
        filterToys({ commit }, { filterObj }) {
            toyService.query(filterObj).then((toys) => commit({ type: 'setToys', toys }))
        }
    }
}

export default toyStore