

<script>
import { Doughnut } from "./bars";
export default {
  props: ["toys"],
  extends: Doughnut,
  data() {
    return {
      typeCounterMap: {},
      types: [],
      coutner: []
    };
  },
  created() {
    this.setInfoReadyForChart();
  },
  methods: {
    setInfoReadyForChart() {
      this.toys.forEach(toy => {
        if (!this.typeCounterMap[toy.type]) {
          this.typeCounterMap[toy.type] = 1;
        } else {
          this.typeCounterMap[toy.type]++;
        }
      });
      this.types = Object.keys(this.typeCounterMap);
      this.coutner = Object.values(this.typeCounterMap);
    }
  },
  mounted() {
    this.renderChart(
      {
        labels: this.types,
        datasets: [
          {
            backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#DD1B16"],
            data: this.coutner
          }
        ]
      },
      { responsive: true, maintainAspectRatio: false }
    );
  }
};
</script>
