<template>
  <section>
    <h1>log-in</h1>
    <form>
      <label>Name</label>
      <input v-model="user.userName" type="text" required>
      <label>Password</label>
      <input v-model="user.userPass" type="password" required>
      <button @click.prevent.stop="onLogIn" type="submit">Log in</button>
    </form>
  </section>
</template>

<script>
import userService from "../service/user.service.js";
// import { eventBus, LOG_IN } from "../services/event-bus.js";
export default {
  name: "log-in",
  data() {
    return {
      user: {
        userName: null,
        userPass: null
      }
    };
  },
  methods: {
    onLogIn() {
      this.$store.dispatch({ type: "logIn", user: this.user });
    }
  }
};
</script>

