          <button @click="$emit('delete' , toy._id)">Delete</button>
          <router-link :to="'/toy/edit/' + toy._id">Edit</router-link>
        </template>
      </div>
      <router-link :to="'/toy/' + toy._id">
        <toy-preview :toy="toy"></toy-preview>
      </router-link>
    </div>
  </section>
</template>

<script>
