<template>
  <section>
    <!-- <md-progress-spinner v-if="!toys" :md-diameter="100" :md-stroke="10" md-mode="indeterminate"></md-progress-spinner> -->
    <log-in v-if="!isLogged"></log-in>
    <!-- <sign-up></sign-up> -->
    <template v-else>
      <toy-filter @filter="onFilter"></toy-filter>
      <hr>
      <router-link to="toy/edit">
        <el-button type="success">Add New Toy</el-button>
      </router-link>
      <hr>
      <toy-list @delete="onDelete" :toys="toys"></toy-list>
      <hr>
      <h1>Some Stats</h1>
      <bar-vue :toys="toys"></bar-vue>
    </template>
  </section>
</template>

<script>
import ToyList from "../components/ToyList.vue";
import toyFilter from "../components/ToyFilter.vue";
import barVue from "../components/bar.vue";
import signUp from "../components/sign-up.vue";
import logIn from "../components/log-in.vue";
export default {
  components: {
    ToyList,
    toyFilter,
    barVue,
    signUp,
    logIn
  },
  data() {
    return {};
  },
  created() {
    // setTimeout(() => {
    this.$store.dispatch({ type: "checkIfLogged" }).then(() => {
      this.$store.dispatch({ type: "getToys" });
    });
    // }, 1500);
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    isLogged() {
      return this.$store.getters.isLogged;
    }
  },
  methods: {
    onDelete(toyId) {
      this.$store.dispatch({ type: "deleteToy", toyId });
    },
    onFilter(filterObj) {
      this.$store.dispatch({ type: "filterToys", filterObj });
    }
  }
};
</script>

