

import storageService from './storage-service.js'
import axios from 'axios'
export default {
    signUp,
    logIn,
    logout,
    getLoggedInUser
}

const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? ''
    : '//localhost:3003'

const API_KEY = 'http://localhost:3003'

var loggedInUser = storageService.load('loggedInUser')

function signUp(userName, userPass) {
    var user = {
        userName,
        userPass
    }
    return axios.post('/api/signUp', user)
        .then((res) => {
            console.log('user signed up!!')
            loggedInUser = user
            storageService.store('loggedInUser', loggedInUser)
        })
        .catch(err => {
            console.log('not signed up :(')
            return reject()
        })
}

function logIn(user) {

    return axios.post(`${BASE_URL}/login`, user).then(res => res.data)
    // storageService.store('loggedInUser', loggedInUser)
}

function logout() {
    return axios.get('/api/logout')
        .then(res => {
            console.log('RES IS', res);
            storageService.remove('loggedInUser')
            loggedInUser = null
        })
}

function getLoggedInUser() {
    if (loggedInUser) return loggedInUser;
    else return {
        userName: false,
        id: false,
        isAdmin: false
    }
}