import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import userService from '../service/user.service.js'
import storageService from '../service/storage-service.js'


const userStore = {
    strict: true,
    state: {
        isLogged: false,
        loggedUserName: null,
        isAdmin: false
    },
    getters: {
        isLogged(state) {
            return state.isLogged
        },
        loggedUserName(state) {
            return state.loggedUserName
        },
        isAdmin(state) {
            var user = storageService.load('loggedInUser')
            if (user.isAdmin === 'true') state.isAdmin = true
            return state.isAdmin
        }
    },
    mutations: {
        userLogged(state, { user }) {
            state.isLogged = true;
            state.loggedUserName = user.userName;
            if (user.isAdmin === 'true') state.isAdmin = true;
        },
        setIfUserIsLogged(state, { isLogged }) {
            state.isLogged = isLogged
        }
    },
    actions: {

        logIn({ commit }, { user }) {
            userService.logIn(user)
                .then((user) => {
                    storageService.store('loggedInUser', user)
                    commit({ type: 'userLogged', user })
                })
                .catch(() => {
                    console.log("WRONG");
                });
        },
        checkIfLogged({ commit }) {
            var isLogged = storageService.load('loggedInUser')
            commit({ type: 'setIfUserIsLogged', isLogged })
        }
    }
}

export default userStore