import Vue from 'vue'
import Router from 'vue-router'
import ToyApp from './views/ToyApp.vue'
import toyDetails from './views/ToyDetails.vue'
import toyEdit from './views/ToyEdit.vue'
import About from './views/About.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (to, from, savedPosition) => ({ y: 99 }),
  routes: [
    {
      path: '/',
      name: 'toys',
      component: ToyApp
    }, {
      path: '/',
      name: 'logIn',
      component: ToyApp
    }, {
      path: '/toy/edit/:id?',
      name: 'toyEdit',
      component: toyEdit
    }, {
      path: '/toy/:id',
      name: 'toyDetails',
      component: toyDetails
    }, {
      path: '/about',
      name: 'about',
      component: About
    }


  ]
})
