<template>
  <section v-if="toyToShow">
    <h1>{{toyToShow.name}}</h1>
    <ul>
      <li>Price: {{toyToShow.price}}</li>
      <li>Type: {{toyToShow.type}}</li>
      <li>In stock: {{toyToShow.inStock}}</li>
    </ul>
  </section>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {
    const toyId = this.$route.params.id;
    this.$store.dispatch({ type: "getToy", toyId });
  },
  computed: {
    toyToShow() {
      return this.$store.getters.toy;
    }
  },
  methods: {}
};
</script>

