<template>
  <section>
    <h1>sign-up</h1>
    <form>
      <label>Name</label>
      <input v-model="userName" type="text" required>
      <label>Password</label>
      <input v-model="userPass" type="password" required>
      <button @click.prevent.stop="onSignUp" type="submit">Sign Up</button>
    </form>
  </section>
</template>

<script>
// import logService from '../services/log-service.js'
// import { eventBus, LOG_IN } from "../services/event-bus.js";
export default {
  name: "sign-up",
  created() {},
  data() {
    return {
      userName: null,
      userPass: null
    };
  },
  methods: {
    onSignUp() {
      if (this.userPass && this.userName) {
        logService
          .signUp(this.userName, this.userPass)
          .then(() => {
            this.$router.push("/bug");
            eventBus.$emit(LOG_IN, true);
          })
          .catch(() => console.log("......."));
      }
    }
  }
};
</script>

