import axios from 'axios'

export default {
    query, getToyById, deleteToy, updateToy, addToy
}


const BASE_URL = (process.env.NODE_ENV !== 'development')
    ? ''
    : '//localhost:3003'

// const API_KEY = 'http://localhost:3003'

function query(filterObj) {

    if (filterObj) {

        let filterQuery = '?';
        if (filterObj.name) filterQuery += `name=${filterObj.name}&`
        if (filterObj.type !== 'All' && filterObj.type) filterQuery += `type=${filterObj.type}&`
        if (filterObj.inStock !== 'All' && filterObj.inStock) filterQuery += `inStock=${filterObj.inStock}&`
        filterQuery += `sortBy=${filterObj.sortBy}`
        console.log(filterQuery);

        return axios.get(`${BASE_URL}/toys/${filterQuery}`).then(filterdToys => filterdToys.data)
    }
    else return axios.get(`${BASE_URL}/toys`).then(toys => toys.data)
}

function getToyById(toyId) {
    return axios.get(`${BASE_URL}/toy/${toyId}`).then(toy => toy.data)
}

function deleteToy(toyId) {
    return axios.delete(`${BASE_URL}/toy/${toyId}`)
}
function updateToy(toy) {
    return axios.put(`${BASE_URL}/toy/${toy._id}`, toy)
}
function addToy(toy) {
    toy.createdAt = new Date().getTime()
    return axios.post(`${BASE_URL}/toy`, toy)
}