<template>
  <section>
    <h1>{{toyId ? 'Edit' : 'Add'}}</h1>
    <form>
      <label>Name:</label>
      <input v-model="toy.name">
      <label>Price:</label>
      <input v-model="toy.price">
      <label>Type:</label>
      <select v-model="toy.type">
        <option value="Adult">Adult</option>
        <option value="Educational">Educational</option>
        <option value="Funny">Funny</option>
      </select>
      <label>In stock:</label>
      <select v-model="toy.inStock">
        <option value="true">In stock</option>
        <option value="false">Out of stock</option>
      </select>
      <button v-if="toyId" @click.prevent="onUpdate" type="submit">Update</button>
      <button v-else @click.prevent="onAdd" type="submit">Add</button>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      toy: {
        name: "",
        price: "",
        type: "",
        inStock: ""
      },
      toyId: null
    };
  },
  created() {
    this.toyId = this.$route.params.id;
    if (this.toyId) {
      this.$store
        .dispatch({ type: "getToy", toyId: this.toyId })
        .then(toy => (this.toy = this.$store.getters.toy));
    }
  },
  methods: {
    onUpdate() {
      this.$store.dispatch({ type: "updateToy", toy: this.toy });
      setTimeout(() => {
        this.$router.push("/");
      }, 600);
    },
    onAdd() {
      if (
        this.toy.name &&
        this.toy.price &&
        this.toy.type &&
        this.toy.inStock
      ) {
        this.$store.dispatch({ type: "addToy", toy: this.toy });
        setTimeout(() => {
          this.$router.push("/");
        }, 600);
      }
    }
  }
};
</script>

<style scoped>
section {
  display: flex;
  align-items: center;
  flex-direction: column;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
}
</style>
