<template>
  <div>
    <div>
      <h2>Search and add a pin</h2>
      <label>
        <gmap-autocomplete @place_changed="setPlace"></gmap-autocomplete>
        <button @click="addMarker">Add</button>
      </label>
      <br>
    </div>
    <br>
    <gmap-map :center="center" :zoom="12" style="width:100%;  height: 400px;">
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>

    <div class="btn-wrapper">
      <button @click="onBtn(0)">Tel Aviv</button>
      <button @click="onBtn(1)">Ramat Gan</button>
      <button @click="onBtn(2)">Haifa</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GoogleMap",
  data() {
    return {
      // default to Montreal to keep it simple
      // change this to whatever makes sense
      center: { lat: 45.508, lng: -73.587 },
      markers: [
        {
          position: {
            lat: 32.0882124,
            lng: 34.80273179999995
          }
        },
        {
          position: {
            lat: 32.08379899999999,
            lng: 34.81545099999994
          }
        },
        {
          position: {
            lat: 32.7940463,
            lng: 34.98957100000007
          }
        }
      ],
      places: [],
      currentPlace: null
    };
  },

  mounted() {
    this.geolocate();
  },

  methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    onBtn(idx) {
      console.log(idx);

      this.center = this.markers[idx].position;
    }
  }
};
</script>

<style scoped lang="scss">
.btn-wrapper {
  margin-top: 20px;
  button {
    border: none;
    background-color: lightseagreen;
    padding: 10px;
    margin: 5px;
    cursor: pointer;
  }
}
</style>